/**
* variables.scss
*
* @author nxxinf
* @github https://github.com/fangnx
* @created 2019-09-15 22:20:48
* @last-modified 2019-09-17 10:55:32
*/
.summary {
  height: 100vh; }

.background {
  background: #212121;
  background-size: cover;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  z-index: -10;
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%; }

.content {
  color: #ffcc80;
  font-family: "Alegraya Sans", sans-serif;
  position: relative;
  top: 5%; }

.fullName {
  font-size: 3.6em;
  font-weight: 500;
  margin-top: 120px; }

.fullName > .first {
  color: #ef5350;
  display: inline;
  margin-right: 0.25em; }

.fullName > .last {
  display: inline; }

.roles {
  font-size: 1.4em;
  font-weight: 500;
  margin-top: 35px;
  line-height: 1.4em; }

.roles > .bullet {
  color: #212121;
  margin-left: 5px;
  margin-right: 5px; }

.roles > .role:hover {
  padding-bottom: 4px !important;
  border-bottom: 4px solid #ffe0b2 !important; }

.links {
  color: #ffcc80;
  margin-top: 35px; }

.links > div {
  display: inline; }

.links i {
  font-size: 2.4em;
  width: 1.4em; }

.audioAnimation {
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  position: relative;
  width: 70px; }

.audioAnimation span {
  width: 10px;
  height: 60px;
  top: 0px;
  position: absolute;
  background: #ef5350;
  -webkit-animation: audio 0.5s infinite ease; }

.audioAnimation span:first-child {
  left: 0px;
  -webkit-animation-delay: 0.2s; }

.audioAnimation span:nth-child(2) {
  left: 20px;
  -webkit-animation-delay: 0.4s; }

.audioAnimation span:nth-child(3) {
  left: 40px;
  -webkit-animation-delay: 0.6s; }

.audioAnimation span:nth-child(4) {
  left: 60px;
  -webkit-animation-delay: 0.8s; }

.audioAnimation span:nth-child(5) {
  left: 80px;
  -webkit-animation-delay: 1s; }

@-webkit-keyframes audio {
  0% {
    height: 24px; }
  30% {
    height: 30px; }
  60% {
    height: 48px; }
  80% {
    height: 36px; }
  100% {
    height: 20px; } }

@keyframes audio {
  0% {
    height: 24px; }
  30% {
    height: 30px; }
  60% {
    height: 48px; }
  80% {
    height: 36px; }
  100% {
    height: 20px; } }

